* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  font-size: 16px;
  min-width: 320px;
  position: relative;
  font-family: "Roboto", sans-serif;
  color: #4E4E4E;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
}

h1, h2, h3, h4 {
  font-weight: normal;
}

img, svg {
  max-height: 100%;
  max-width: 100%;
  width: auto;
  height: auto;
}

input, button, textarea {
  outline: none;
}

input:focus, button:focus, textarea:focus {
  outline: none;
}

input, textarea {
  padding: 10px;
}

textarea {
  resize: none;
}

ul {
  list-style: none;
  margin-bottom: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
  color: #F89D92;
}

button, .btn {
  border: 2px solid #EA9085;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 15px;
  border-radius: 10px;
  min-width: 120px;
  background: #EA9085;
  color: #ffffff;
  position: relative;
  overflow: hidden;
}

button:hover, .btn:hover {
  background: #F89D92;
  border-color: #F89D92;
  color: #ffffff;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.15);
}

.animate, a, button, .btn {
  transition: all 0.3s ease-out;
}

.no_scroll {
  overflow: hidden;
}

.no_scroll:after {
  content: '';
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1;
}

.hidden {
  display: none !important;
}

.img_cover {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100%;
  width: 100%;
}

.header {
  flex: 0 0 auto;
  height: 80px;
  background: #3f3838;
}

.burger {
  height: 25px;
  width: 30px;
  position: relative;
  transition: .5s ease-in-out;
  cursor: pointer;
  z-index: 1;
}

.burger span {
  display: block;
  position: absolute;
  height: 4px;
  width: 100%;
  background: #4E4E4E;
  border-radius: 4px;
  opacity: 1;
  left: 0;
  transition: .25s ease-in-out;
}

.burger span:nth-child(1) {
  top: 0px;
}

.burger span:nth-child(2), .burger span:nth-child(3) {
  top: 10px;
}

.burger span:nth-child(4) {
  top: 20px;
}

.burger.active span:nth-child(1) {
  top: 10px;
  width: 0%;
  left: 50%;
}

.burger.active span:nth-child(2) {
  transform: rotate(45deg);
}

.burger.active span:nth-child(3) {
  transform: rotate(-45deg);
}

.burger.active span:nth-child(4) {
  top: 10px;
  width: 0%;
  left: 50%;
}

.footer {
  flex: 0 0 auto;
  height: 100px;
}

.main {
  flex: 1 0 auto;
  overflow: hidden;
  padding-top: 80px;
}

.stake {
  background: #192c39;
}

.stake-block-left {
  background: #213743;
  width: 300px;
}

.stake-button {
  font-weight: 600;
  font-size: 12px;
  height: 34px;
  background: #142a37;
  color: #b1bad3;
  border: none;
}

.stake-button:hover {
  color: #b1bad3;
  background: #425a6c;
}

.stake .button-manual {
  border-radius: 3px 0 0 3px;
  min-width: 112px;
}

.stake .button-auto {
  border-radius: 0;
  background: #2f4553;
  min-width: 98px;
}

.stake .button-auto:hover {
  color: #b1bad3;
  background: #425a6c;
}

.stake .button-plus {
  border-radius: 0 3px 3px 0;
  background: #2f4553;
  min-width: 60px;
}

.stake .button-plus:hover {
  color: #b1bad3;
  background: #425a6c;
}

.stake-svg {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentcolor;
  fill: currentcolor;
}

.stake-text {
  color: #b1bad3;
  font-weight: 600;
  font-size: 14px;
}

.stake .color-info {
  color: #b1bad3;
  cursor: help;
}

.stake-label {
  position: relative;
}

.stake-coin-svg {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentcolor;
  fill: currentcolor;
  position: absolute;
  top: 50%;
  transform: translate(0px, -50%);
  pointer-events: none;
  color: #b1bad3;
  cursor: text;
  right: 0.75em;
}

.stake-input {
  color: white;
  background: #0f212e;
  border: 2px solid #2f4553;
  border-radius: 3px 0 0 3px;
  font-weight: 600;
  transition: all 200ms ease-out 0s;
  width: 100%;
  height: 40px;
  padding: 0.5em 2em 0.5em 0.5em;
  border-right: none;
}

.stake .btn-input {
  background: #2f4553;
  min-width: 40px;
  height: 40px;
  border: none;
  border-radius: 0;
