
// HEADER
.header {
	flex: 0 0 auto;
	height: 80px;
	background: rgb(63, 56, 56);
}
.burger {
	height: 25px;
	width: 30px;
	position: relative;
	transition: .5s ease-in-out;
	cursor: pointer;
    z-index: 1;
	span {
		display: block;
		position: absolute;
		height: 4px;
		width: 100%;
		background: $text;
		border-radius: 4px;
		opacity: 1;
		left: 0;
		transition: .25s ease-in-out;
		&:nth-child(1) {
  			top: 0px;
		}
		&:nth-child(2),
		&:nth-child(3) {
			top: 10px;
		}
		&:nth-child(4) {
			top: 20px;
		}
	}
	&.active {
		span {
			&:nth-child(1) {
				top: 10px;
				width: 0%;
				left: 50%;
			}
			&:nth-child(2) {
				transform: rotate(45deg);
			}
			&:nth-child(3) {
				transform: rotate(-45deg);
			}
			&:nth-child(4) {
				top: 10px;
				width: 0%;
				left: 50%;
			}
		}
	}
}
